.movies {display: flex; align-items: flex-start; flex-wrap: wrap;  justify-content: space-between; width: calc(100% - 50px); padding-top: 100px;}
.movie {display: flex; align-items: flex-start; flex: 0 0 46%; margin-bottom: 50px; padding: 15px; background-color: #fff; box-shadow: 2px 2px 10px rgba(0,0,0,0.4); box-sizing: border-box;}
.movie_thumb {
  position: relative; 
  top: -35px; 
  width: 120px; 
  height:auto; 
  margin-right: 20px; 
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
  0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}
.movie_data {}
.movie_title {margin-bottom: 10px; font-size: 30px; color: #333; line-height: 1.3; word-break: keep-all;}
.movie_year {margin-bottom: 10px; font-size: 12px; color: #777; font-weight: 400;}
.movie_data .genres {display: flex; flex-wrap: wrap; margin-bottom: 20px; color: #777;}
.movie_data .genres > .genres_genre {margin-right: 10px; font-size: 12px; line-height: 1.3;}
.movie_summary {word-break: keep-all; color: #777; font-weight: 300; line-height: 1.4;}